<template>
  <div id="agiotage">
    <!-- Select Holding or Target Currency -->
    <ul class="selectTarget">
      <li @click="selectedHolding=true" :class="{'selectedHolding':false}">{{$t("message.transferPayment.HoldingCurrency")}}</li>
      <li @click="selectedHolding=false" :class="{'selectedHolding':false}">{{$t("message.transferPayment.TargetCurrency")}}</li>
    </ul>
    <!-- Select Currency -->
    <ul class="selectCurrency">
      <li @click="currencyPairs.base.clicked = !currencyPairs.base.clicked;currencyPairs.counter.clicked=false;">
        <i :class="`currency-icon-${currencyPairs.base.name}`"></i>
        <span>{{currencyPairs.base.name}}</span>
        <i :class="`triangle_${currencyPairs.base.clicked?'clicked':'default'}`"></i>
      </li>
      <li @click="currencyPairs.counter.clicked = !currencyPairs.counter.clicked;currencyPairs.base.clicked=false;">
        <i :class="`currency-icon-${currencyPairs.counter.name}`"></i>
        <span>{{currencyPairs.counter.name}}</span>
        <i :class="`triangle_${currencyPairs.counter.clicked?'clicked':'default'}`"></i>
      </li>
    </ul>
    <!-- selector -->
    <div style="display:flex;">
      <div style="position:relative;width:50%;">
        <div v-show="currencyPairs.base.clicked" style="position:absolute;top:100%;left:50%;margin-left:-0.95rem;margin-top:-0.15rem;">
          <JcSelect v-model="currencyPairs.base.name" :data="currencyList" :align="'left'" :width="'1.9rem'" :borderColor="'#559df7'" :itemSpacing="'0.1rem'" :fontSize="'0.30rem'" @close="currencyPairs.base.clicked=false;"></JcSelect>
        </div>
      </div>
      <div style="position:relative;width:50%;">
        <div v-show="currencyPairs.counter.clicked" style="position:absolute;top:100%;left:50%;margin-left:-0.95rem;margin-top:-0.15rem;">
          <JcSelect v-model="currencyPairs.counter.name" :data="currencyList" :align="'left'" :width="'1.9rem'" :borderColor="'#559df7'" :itemSpacing="'0.1rem'" :fontSize="'0.30rem'" @close="currencyPairs.counter.clicked=false;"></JcSelect>          
        </div>
      </div>
    </div>
    <!-- Input Amount to Agiotage -->
    <div class="amountInput">
      <div>
        <input type="number" v-model="form.inputHoldingCount" @input="holdingInputChange" @focus="form.inputHoldingCount=''" @blur="holdingInputBlur">
      </div>
      <p style="height:78%;border:0.015rem solid #e0e3e8;"></p>
      <div style="justify-content:flex-end;">
        <input type="number" v-model="form.inputTargetCount" style="text-align:right;" @input="targetInputChange" @focus="form.inputTargetCount=''" @blur="targetInputBlur">
      </div>
    </div>
    <!-- Show Exchange Rate -->
    <ul class="exchangeRate">
      <li style="justify-content:flex-end;">{{$t("message.transferPayment.exchangeRate")}}：</li>
      <li style="min-width:30%;background-color:#e8eaf0;padding:0.05rem 0;border-radius:0.1rem;text-indent:0.2rem;color:#4d4d4d;">{{getExchangeRate}}</li>
      <!-- To Compare Exchange Rate  -->
      <li style="border-radius:0.22rem;color:#fff;background-color:#559df7;margin-left:0.2rem;padding:0.04rem 0.1rem;justify-content:center;">{{$t("message.transferPayment.exchangeRateCompare")}}</li>
    </ul>
    <!-- Show Transaction Summary -->
    <ol class="transactionSummary">
      <li style="color:#555b61;justify-content:center;">{{$t("message.transferPayment.transactionSummary")}}</li>
      <li>
        <span>{{$t("message.transferPayment.remittanceAmount")}}</span>
        <span>{{form.inputHoldingCount||0}} <span class="monoSpace">{{currencyPairs.base.name}}</span></span>
      </li>
      <li>
        <span>{{$t("message.transferPayment.transactionCost")}}</span>
        <span>{{getTrandeCostPrice||0}} <span class="monoSpace">{{currencyPairs.base.name}}</span></span>
      </li>
      <li>
        <span>{{$t("message.transferPayment.amountOfConversion")}}</span>
        <span>{{getAmountOfConvert||0}} <span class="monoSpace">{{currencyPairs.counter.name}}</span></span>
      </li>
      <li>
        <span>{{$t("message.transferPayment.receivableAmount")}}</span>
        <span>{{getReceivableAmount||0}} <span class="monoSpace">{{currencyPairs.counter.name}}</span></span>
      </li>
    </ol>
    <!-- Input Information and Confirm payment -->
    <ol class="inputInformation">
      <li style="width:100%;position:relative;">
        <!-- Input Payee Name -->
        <label>{{$t("message.transferPayment.payee")}}：</label>
        <!-- <div style="position:relative;width:70%;margin-right:0.15rem;"> -->
          <input v-model="form.payeeName" type="text" style="margin-right:0.2rem;">
          <!-- Contacts icon -->
          <i class="contacts_icon positionIcon" style="margin-top: -0.15rem;right:0.5rem;"></i>
        <!-- </div> -->
        <!-- Scaning icon -->
        <i class="scanning_icon" style="min-width:0.32rem;"></i>
      </li>
      <li style="align-items:flex-start;">
        <!-- Input wallet Address -->
        <label style="margin-top:0.1rem;">{{$t("message.transferPayment.walletAddress")}}：</label>
        <textarea v-model="form.walletAddress" style="width:100%;height:0.9rem;padding:0.1rem 0;vertical-align:middle;outline:none;"></textarea>
      </li>
      <li>
        <!-- Choose Purpose -->
        <label style="margin-top:0.1rem;">{{$t("message.transferPayment.purpose")}}：</label>
        <div style="position:relative;width:100%;">
          <input style="width:100%;" v-model="form.purpose" type="text">
          <div @click="choosingPurpose=!choosingPurpose;" style="position:absolute;top:0;right:0;width:0.8rem;height:0.7rem;">
            <i class="blackArrow_icon positionIcon" :style="{'margin-top':choosingPurpose?'-0.1rem':'-0.12rem','transform':choosingPurpose?'rotate(90deg)':'rotate(0deg)'}"></i>
          </div>
          <div style="width:100%;position:absolute;top:0.9rem;left:0;z-index:1;">
            <JcSelect v-show="choosingPurpose" v-model="form.purpose" @close="choosingPurpose=false;" :data="purposeList" :width="'100%'" :borderColor="'#559df7'" :align="'left'" :fontSize="'0.28rem'" style="color:#616569;"></JcSelect>
          </div>
        </div>
      </li>
      <li style="justify-content:center;">
        <button @click="confirmPay" class="confirmBtn" :class="{'confirm_Btn':checkInput}">{{$t("message.transferPayment.confirmPay")}}</button>
      </li>
    </ol>
  </div>
</template>
<script>
import { BigNumber } from "bignumber.js";
import JcSelect from "@/components/jc_select";
export default {
  name: "agiotage",
  components: { JcSelect },
  data() {
    return {
      selectedHolding: true,
      choosingPurpose: false,
      form: {
        inputHoldingCount: "",
        inputTargetCount: "",
        payeeName: "",
        walletAddress: "",
        purpose: this.$t("message.transferPayment.overseasInvestment")
      },
      purposeList: [
        { name: this.$t("message.transferPayment.overseasInvestment") },
        { name: this.$t("message.transferPayment.crossBorderTransfer") },
        { name: this.$t("message.transferPayment.crossBorderPayment") }
      ],
      currencyPairs: {
        base: { name: "EGP", clicked: false },
        counter: { name: "CNT", clicked: false }
      },
      currencyList: []
    };
  },
  mounted() {
    this.form.inputHoldingCount = 1000;
    this.form.inputTargetCount = this.getAmountOfConvert;
    setTimeout(() => {
      this.getCurrencyListList();
    }, 300);
  },
  methods: {
    confirmPay() {
      if (!this.checkInput) {
        return;
      }
      alert("开始汇兑支付");
    },
    holdingInputChange(e) {
      let val = e.target.value;
      if (!val || val === "" || val <= 0) {
        return;
      }
      this.form.inputTargetCount = new BigNumber(this.getAmountOfConvert)
        .decimalPlaces(5)
        .toNumber();
    },
    holdingInputBlur() {
      let val = this.form.inputHoldingCount;
      if (!val || val <= 0 || val === "") {
        this.form.inputHoldingCount = this.getAmountByConvertCount;
      }
    },
    targetInputChange(e) {
      let val = e.target.value;
      if (!val || val === "" || val <= 0) {
        return;
      }
      this.form.inputHoldingCount = new BigNumber(this.getAmountByConvertCount)
        .decimalPlaces(5)
        .toNumber();
    },
    targetInputBlur() {
      let val = this.form.inputTargetCount;
      if (!val || val <= 0 || val === "") {
        this.form.inputTargetCount = this.getAmountOfConvert;
      }
    },
    getCurrencyListList() {
      let len = this.coinList.length;
      if (len <= 0) {
        return;
      }
      for (i = 0; i < len; i++) {
        this.currencyList.push({
          name: this.coinList[i].label,
          icon: `currency-icon-${this.coinList[i].label}`
        });
      }
    }
  },
  computed: {
    coinList() {
      return this.$store.getters.coins;
    },
    getTrandeCostPrice() {
      return new BigNumber(this.form.inputHoldingCount)
        .multipliedBy(new BigNumber(this.getTradeCostRate))
        .decimalPlaces(5)
        .toNumber();
    },
    getAmountOfConvert() {
      if (this.form.inputHoldingCount !== "") {
        return new BigNumber(this.form.inputHoldingCount)
          .multipliedBy(new BigNumber(this.getExchangeRate))
          .decimalPlaces(5)
          .toNumber();
      }
    },
    getAmountByConvertCount() {
      if (this.form.inputTargetCount !== "") {
        return new BigNumber(this.form.inputTargetCount)
          .dividedBy(this.getExchangeRate)
          .decimalPlaces(5)
          .toNumber();
      }
    },
    getReceivableAmount() {
      return new BigNumber(this.getAmountOfConvert).decimalPlaces(5).toNumber();
    },
    getExchangeRate() {
      let rate = 5.775;
      return new BigNumber(rate).decimalPlaces(5).toNumber();
    },
    getTradeCostRate() {
      let rate = 0.005;
      return new BigNumber(rate).decimalPlaces(5).toNumber();
    },
    checkInput() {
      return (
        this.form.inputHoldingCount > 0 &&
        this.form.payeeName !== "" &&
        this.form.walletAddress !== "" &&
        this.form.purpose !== "" &&
        this.currencyPairs.base.name !== "" &&
        this.currencyPairs.counter.name !== ""
      );
    }
  }
};
</script>
<style lang="scss" scoped>
#agiotage {
  display: flex;
  flex-flow: column;
  padding: 0.88rem 0.4rem 0;
}
.selectTarget {
  display: flex;
  width: 100%;
  font-size: 0.3rem;
  margin-top: 0.2rem;
  color: #5e6469;
  li {
    width: 50%;
    padding-bottom: 0.15rem;
    border-bottom: 0.04rem solid #e0e3e8;
  }
  .selectedHolding {
    color: #559df7;
    border-bottom: 0.04rem solid #559df7;
    font-weight: bold;
  }
}
.selectCurrency {
  display: flex;
  width: 100%;
  font-size: 0.3rem;
  margin-top: 0.1rem;
  li {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 50%;
    padding: 0.3rem 0;
    i {
      position: relative;
      top: -0.02rem;
    }
    span {
      margin: 0 0.2rem 0 0.1rem;
    }
  }
}
.amountInput {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 0.88rem;
  width: 86%;
  margin: 0 auto;
  border: 0.02rem solid #005c9c;
  color: #005c9c;
  border-radius: 0.44rem;
  font-size: 0.3rem;
  font-weight: bold;
  div {
    display: flex;
    align-items: center;
    width: 42%;
    height: 78%;
    input {
      width: 100%;
      border: none;
      background-color: #fafafa;
    }
  }
}
.exchangeRate {
  width: 100%;
  min-height: 0.44rem;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0.4rem auto 0;
  color: #373e44;
  font-size: 0.26rem;
  li {
    height: 100%;
    display: flex;
    align-items: center;
    white-space: nowrap;
  }
}
.transactionSummary {
  display: flex;
  flex-flow: column;
  margin-top: 0.5rem;
  .monoSpace {
    font-family: "Consolas", "Monaco", "monospace";
  }
  li {
    display: flex;
    align-items: center;
    justify-content: space-between;
    color: #6e7880;
    font-size: 0.28rem;
    margin-bottom: 0.15rem;
  }
  li:last-child,
  li:first-child {
    border-bottom: 0.02rem solid #f1f1f1;
    padding-bottom: 0.16rem;
  }
}
.inputInformation {
  width: 100%;
  font-size: 0.26rem;
  color: #4d4d4d;
  li {
    width: 100%;
    display: flex;
    align-items: center;
    margin: 0.2rem 0;
    label {
      min-width: 1.3rem;
      max-width: 1.5rem;
      text-align: left;
    }
    input,
    textarea {
      width: 100%;
      height: 0.7rem;
      border: 0.01rem solid #b1bec6;
      border-radius: 0.1rem;
      text-indent: 0.2rem;
      font-size: 0.3rem;
      &:focus {
        border: 0.01rem solid #559df7;
      }
    }
    .positionIcon {
      position: absolute;
      top: 50%;
      right: 0;
      margin-right: 0.2rem;
    }
  }
}
.confirmBtn {
  height: 0.76rem;
  min-width: 32%;
  padding: 0 0.2rem;
  background-color: #3f7bff;
  color: #fff;
  margin: 0.1rem 0 3.2rem 0;
  border-radius: 0.1rem;
  opacity: 0.7;
}
.confirm_Btn {
  opacity: 1;
}
</style>



